<template>
  <div class="navbar-container d-flex content align-items-center">

    <!-- Nav Menu Toggler -->
    <ul class="nav navbar-nav d-xl-none">
      <li class="nav-item">
        <b-link
          class="nav-link"
          @click="toggleVerticalMenuActive"
        >
          <feather-icon
            icon="MenuIcon"
            size="21"
          />
        </b-link>
      </li>
    </ul>

    <!-- Left Col -->
    <div class="bookmark-wrapper align-items-center flex-grow-1 d-none d-lg-flex">
      <dark-Toggler class="d-none d-lg-block" />
    </div>

    <b-navbar-nav class="nav align-items-center ml-auto">
      <b-nav-item-dropdown
        right
        toggle-class="d-flex align-items-center dropdown-user-link"
        class="dropdown-user"
      >
        <template #button-content>
          <div class="d-sm-flex d-none user-nav">
            <p class="user-name font-weight-bolder mb-0">
              {{ data.username }}
            </p>
            <span class="user-status">Admin</span>
          </div>
          <b-avatar
            size="40"
            variant="light-primary"
            badge
            :src="`${data.profile_image ? imageUrl + data.profile_image : Image}`"
            class="badge-minimal"
            badge-variant="success"
          />
        </template>
        <b-dropdown-item
          link-class="d-flex align-items-center"
          @click="manageProfile"
        >
          <feather-icon
            size="16"
            icon="UserIcon"
            class="mr-50"
          />
          <span>Edit Profile</span>
        </b-dropdown-item>
        <b-dropdown-item
          link-class="d-flex align-items-center"
          @click="changePassword"
        >
          <feather-icon
            size="16"
            icon="UserIcon"
            class="mr-50"
          />
          <span>Edit Password</span>
        </b-dropdown-item>
        <b-dropdown-item
          link-class="d-flex align-items-center"
          @click="logout"
        >
          <feather-icon
            size="16"
            icon="LogOutIcon"
            class="mr-50"
          />
          <span>Logout</span>
        </b-dropdown-item>
      </b-nav-item-dropdown>
    </b-navbar-nav>
  </div>
</template>

<script>
import {
  BLink, BNavbarNav, BNavItemDropdown, BDropdownItem, BAvatar,
} from 'bootstrap-vue'
import DarkToggler from '@core/layouts/components/app-navbar/components/DarkToggler.vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import Image from '@/assets/images/avatars/13-small.png'

export default {
  components: {
    BLink,
    BNavbarNav,
    BNavItemDropdown,
    BDropdownItem,
    BAvatar,

    // Navbar Components
    DarkToggler,
  },
  props: {
    toggleVerticalMenuActive: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      Image,
      imageUrl: 'https://dev.api.the-netwerk.com/storage/app/',
      username: '',
      data: {},
      localStorage: null,
    }
  },
  computed: {
    userData() {
      return JSON.parse(window.localStorage.getItem('userData'))
    },
  },
  // Watch On Store userData.user
  watch: {
    userData() {
      this.getData()
    },
  },
  mounted() {
    this.localStorage = JSON.parse(window.localStorage.getItem('userData'))
    if (window.localStorage.getItem('userData')) {
      this.username = this.localStorage.username.replace(/^./, this.localStorage.username[0].toUpperCase())
    } else {
      // Router Push To Login
      this.$router.push({ path: '/login' })
    }
    this.getData()
  },
  methods: {
    logout() {
      // const email = localStorage.getItem('userData')
      const data = {
        email: this.localStorage.email,
      }
      this.$http.post('logout', data).then(response => {
        if (response.data.status) {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: 'Logout Success',
              icon: 'EditIcon',
              variant: 'success',
            },
          })
          // Remove Localstorage
          window.localStorage.removeItem('userData')
          window.localStorage.removeItem('token')
          this.$store.commit('userData/logout')
          // Timeout
          setTimeout(() => {
            this.$router.push({ name: 'login' })
          }, 1000)
        } else {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: response.data.message,
              icon: 'AlertTriangleIcon',
              variant: 'danger',
            },
          })
        }
      }).catch(error => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: error,
            icon: 'AlertTriangleIcon',
            variant: 'danger',
          },
        })
      })
    },
    manageProfile() {
      this.$router.push({ name: 'profile' })
    },
    changePassword() {
      this.$router.push({ name: 'change-password' })
    },
    getData() {
      const userData = JSON.parse(window.localStorage.getItem('userData'))
      const data = {
        email: userData.email,
        profile_image: userData.profile_image,
      }
      this.$http.post('getUser', data).then(response => {
        if (response.data.status === true) {
          this.data = response.data.data
          this.$store.commit('userData/setUser', response.data.data)
        }
      })
    },
  },
}
</script>
